body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  padding: 2px 4px;
  font-size: 20px;
  border-radius: 5px;
  min-height: 40px;
  margin: 5px;
}

.form {
  font-size: 20px;
  min-height: 100vh;
  background-image: url(/to-do-app/static/media/concrete_wall.a43a70ea.png);
  width: 20vw;
}
.form h2 {
  text-align: center;
  color: antiquewhite;
  border-bottom: 5px solid #370dcd;
  padding: 0 10px 10px 10px;
}
.form button {
  margin-left: 2%;
  padding: 18px 30px;
  background-color: #370dcd;
  text-transform: uppercase;
  border: 1px solid #370dcd;
  transition: all 0.2s ease;
  box-shadow: 0px 5px 8px 0px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  color: antiquewhite;
}

.form button:hover {
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.25);
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
}
label#date {
  color: antiquewhite;
  padding: 10px;
  margin: 20px;
}
input#important {
  width: 40px;
  height: 40px;
  margin-top: 20px;
}
label#important {
  color: antiquewhite;
  font-size: 25px;
}
.task,
.completed {
  text-align: right;
  margin-right: 10px;
  color: antiquewhite;
}
.task button,
.completed button {
  margin-left: 2%;
  padding: 5px 10px;
  background-color: #370dcd;
  text-transform: uppercase;
  border: 1px solid #370dcd;
  transition: all 0.2s ease;
  box-shadow: 0px 5px 8px 0px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  color: antiquewhite;
}
.task button:hover,
.completed button:hover {
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.25);
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
}
#info {
  text-align: center;
  font-size: 25px;
  color: antiquewhite;
}

#message {
  margin-left: 10%;
  text-align: center;
  font-size: 15px;
  color: antiquewhite;
}

.App {
  display: flex;
  padding: 0;
  margin: 0;
  height: 100%;
  box-sizing: border-box;
  background-color: #370dcd;
}
.active {
  min-height: 100vh;
  background-image: url(/to-do-app/static/media/noisy_net.3f088219.png);
  width: 50vw;
  border-left: 5px solid #370dcd;
}
.done {
  min-height: 100vh;
  background-image: url(/to-do-app/static/media/green-fibers.b1f23a16.png);
  width: 30vw;
  border-left: 5px solid #370dcd;
}
.active h1 {
  text-align: center;
  color: antiquewhite;
  border-bottom: 5px solid #370dcd;
  padding: 0 10px 10px 10px;
}
.done h1 {
  text-align: center;
  color: antiquewhite;
  border-bottom: 5px solid #370dcd;
  padding: 0 10px 10px 10px;
}

@media (max-width: 1024px) {
  .form,
  .active,
  .done {
    width: 33vw;
  }
}
@media (max-width: 700px) {
  .App {
    display: block;
  }
  .form,
  .active,
  .done {
    width: 100%;
  }
}

